export function formatCurrency(value) {
  return new Intl.NumberFormat('he-IL', {
    style: 'currency',
    currency: 'ILS',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  }).format(value);
}

export function formatPercentage(value) {
  return new Intl.NumberFormat('he-IL', {
    style: 'percent',
    minimumFractionDigits: 1,
    maximumFractionDigits: 2,
  }).format(value / 100);
}

export function getPerformanceLabel(performance) {
  const labels = {
    surge: ' ',
    leading: '',
    erosion: '',
  };
  return labels[performance] || performance;
}

export function getPerformanceColor(performance) {
  switch (performance) {
    case 'surge':
      return 'emerald';
    case 'leading':
      return 'emerald';
    case 'erosion':
      return 'rose';
    default:
      return 'slate';
  }
}

export function getPortfolioLabel(id) {
  const labels = {
    solid: '',
    creative: '',
    super_agg: '',
    speculative: '',
    defensive: '',
  };
  return labels[id] || id;
}

export function calculateGoalProgress(currentNet, goal = 100000) {
  return Math.min((currentNet / goal) * 100, 500);
}

export function formatDate(isoString) {
  return new Intl.DateTimeFormat('he-IL', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
  }).format(new Date(isoString));
}
