import React from 'react';
import { motion } from 'framer-motion';
import { TrendingUp, TrendingDown, Award } from 'lucide-react';
import { Card } from '../atoms/Card';
import { formatCurrency, getPerformanceLabel, getPerformanceColor, getPortfolioLabel } from '../utils';

export function PortfolioCard({ portfolio, rank, delay = 0 }) {
  const isPositive = portfolio.delta_pct > 0;
  const performanceColor = getPerformanceColor(portfolio.performance);
  const isRanked1 = rank === 1;

  const badgeColor = performanceColor === 'emerald' ? 'badge-success' : 'badge-warning';

  return (
    <Card delay={delay} hoverable={true} className={isRanked1 ? 'border-emerald-500/30 bg-emerald-500/5' : ''}>
      <div className="flex items-start justify-between mb-4">
        <div className="flex items-start gap-3">
          {isRanked1 && (
            <motion.div
              initial={{ scale: 0, rotate: -180 }}
              animate={{ scale: 1, rotate: 0 }}
              transition={{ duration: 0.5, delay: delay + 0.2 }}
              className="flex-shrink-0"
            >
              <Award className="w-6 h-6 text-emerald-400" />
            </motion.div>
          )}
          <div>
            <h3 className="text-xl font-bold text-slate-50">{getPortfolioLabel(portfolio.id)}</h3>
            <p className="text-sm text-slate-400"> #{rank}</p>
          </div>
        </div>
        <span className={badgeColor}>{getPerformanceLabel(portfolio.performance)}</span>
      </div>

      <div className="grid grid-cols-2 gap-4 mb-4">
        <div>
          <p className="text-xs text-slate-500 mb-1"> </p>
          <p className="text-2xl font-bold text-emerald-400">
            {formatCurrency(portfolio.total_net)}
          </p>
        </div>
        <div>
          <p className="text-xs text-slate-500 mb-1"></p>
          <div className="flex items-center gap-1">
            {isPositive ? (
              <TrendingUp className="w-5 h-5 text-emerald-400" />
            ) : (
              <TrendingDown className="w-5 h-5 text-rose-400" />
            )}
            <span className={`text-2xl font-bold ${isPositive ? 'text-emerald-400' : 'text-rose-400'}`}>
              {Math.abs(portfolio.delta_pct).toFixed(1)}%
            </span>
          </div>
        </div>
      </div>

      {/* Mini chart visualization */}
      <div className="h-1 bg-slate-800 rounded-full overflow-hidden">
        <motion.div
          initial={{ width: 0 }}
          animate={{ width: '100%' }}
          transition={{ duration: 0.8, delay: delay + 0.3, ease: 'easeOut' }}
          className={`h-full ${isPositive ? 'bg-emerald-500' : 'bg-rose-500'}`}
        />
      </div>
    </Card>
  );
}
