import React from 'react';
import { motion } from 'framer-motion';
import { TrendingUp } from 'lucide-react';
import { formatCurrency } from '../utils';

export function GoalProgress({ currentNet, goal = 100000 }) {
  const percentage = (currentNet / goal) * 100;

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5, delay: 0.1 }}
      className="bg-gradient-to-br from-slate-900 to-slate-800 border border-slate-700 rounded-2xl p-8 mb-8"
    >
      <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-6">
        <div className="flex-1">
          <div className="flex items-center gap-2 mb-4">
            <TrendingUp className="w-6 h-6 text-emerald-400" />
            <h2 className="text-2xl font-bold text-slate-50"> </h2>
          </div>
          <p className="text-slate-400 mb-4">  </p>
          
          <div className="flex items-baseline gap-2 mb-6">
            <span className="text-5xl font-bold text-emerald-400">{formatCurrency(currentNet)}</span>
            <span className="text-slate-400">/</span>
            <span className="text-xl text-slate-400">{formatCurrency(goal)}</span>
          </div>

          {/* Progress bar */}
          <div className="w-full bg-slate-700 rounded-full h-2 overflow-hidden">
            <motion.div
              initial={{ width: 0 }}
              animate={{ width: `${Math.min(percentage, 100)}%` }}
              transition={{ duration: 1, ease: 'easeOut' }}
              className="h-full bg-gradient-to-r from-emerald-500 to-emerald-400"
            />
          </div>
        </div>

        <div className="flex flex-col items-center md:items-end">
          <motion.div
            initial={{ scale: 0 }}
            animate={{ scale: 1 }}
            transition={{ duration: 0.5, delay: 0.3, type: 'spring', stiffness: 100 }}
            className="text-6xl font-bold text-emerald-400 mb-2"
          >
            {Math.round(percentage)}%
          </motion.div>
          <p className="text-slate-400 text-sm">
            {percentage > 100 ? ' !' : ' ' + formatCurrency(goal - currentNet)}
          </p>
        </div>
      </div>
    </motion.div>
  );
}
