import React from 'react';
import { motion } from 'framer-motion';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  Cell,
} from 'recharts';
import { Card } from '../atoms/Card';
import { getPortfolioLabel } from '../utils';
import { BarChart3 } from 'lucide-react';

export function PerformanceChart({ portfolios }) {
  const data = portfolios.map(p => ({
    name: getPortfolioLabel(p.id),
    value: p.delta_pct,
    isPositive: p.delta_pct > 0,
  }));

  const getBarColor = (value) => {
    return value >= 0 ? '#34d399' : '#fb7185';
  };

  const CustomTooltip = ({ active, payload }) => {
    if (active && payload && payload.length) {
      return (
        <div className="bg-slate-900 border border-slate-700 rounded-lg p-3">
          <p className="text-slate-50 font-semibold">{payload[0].payload.name}</p>
          <p className={`font-mono text-lg ${payload[0].value >= 0 ? 'text-emerald-400' : 'text-rose-400'}`}>
            {payload[0].value.toFixed(2)}%
          </p>
        </div>
      );
    }
    return null;
  };

  return (
    <Card delay={0.3} hoverable={false}>
      <div className="flex items-center gap-2 mb-6">
        <BarChart3 className="w-6 h-6 text-emerald-400" />
        <h3 className="text-2xl font-bold text-slate-50"> </h3>
      </div>

      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.6, delay: 0.2 }}
        className="w-full h-80"
      >
        <ResponsiveContainer width="100%" height="100%">
          <BarChart
            data={data}
            margin={{ top: 20, right: 30, left: -10, bottom: 20 }}
          >
            <CartesianGrid
              strokeDasharray="3 3"
              stroke="#334155"
              vertical={false}
            />
            <XAxis
              dataKey="name"
              stroke="#94a3b8"
              style={{ fontSize: '12px' }}
              angle={0}
            />
            <YAxis
              stroke="#94a3b8"
              style={{ fontSize: '12px' }}
              label={{ value: '(%)', angle: -90, position: 'insideLeft' }}
            />
            <Tooltip content={<CustomTooltip />} cursor={{ fill: 'rgba(148, 163, 184, 0.1)' }} />
            <Bar
              dataKey="value"
              fill="#34d399"
              radius={[8, 8, 0, 0]}
              animationDuration={800}
            >
              {data.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={getBarColor(entry.value)} />
              ))}
            </Bar>
          </BarChart>
        </ResponsiveContainer>
      </motion.div>

      <div className="mt-6 grid grid-cols-3 gap-4">
        <div className="bg-slate-800 rounded-lg p-4">
          <p className="text-xs text-slate-400 mb-1"></p>
          <p className="text-2xl font-bold text-emerald-400">
            {data.filter(d => d.isPositive).length}/{data.length}
          </p>
        </div>
        <div className="bg-slate-800 rounded-lg p-4">
          <p className="text-xs text-slate-400 mb-1"></p>
          <p className="text-2xl font-bold text-rose-400">
            {data.filter(d => !d.isPositive).length}/{data.length}
          </p>
        </div>
        <div className="bg-slate-800 rounded-lg p-4">
          <p className="text-xs text-slate-400 mb-1"></p>
          <p className="text-2xl font-bold text-slate-50">
            {Math.max(...data.map(d => d.value)).toFixed(0)}%
          </p>
        </div>
      </div>
    </Card>
  );
}
